/* comment */

.sp1_task_comments{
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}


.sp1_task_write_comment{
    display: flex; 
    gap: 10px;
}

.sp1_task_comment_send_box{ 
    display: flex;
    width: 100%;
    
    & .__avatar{
        width: 48px;
        height: 48px;
        display: inline-block;

    }

    & .__send-box{
        flex: 1;
        display: flex;
        align-items: center;
        width: 100%;
        max-width: 700px;

       & .__box{
            width: 100%;
            background: #fff;
            padding: 10px 12px;
            border: 1px solid #e1e1e1;
            border-radius: 6px;
            cursor: pointer;
            span{
                color: #777;
            } 

            &.__reply_text{
                background: #f5f9fd;
                border: none;
                padding-left: 14px;
                border-radius: 0;

                div{  
                    border-left: 1px solid rgba(53, 129, 222, .3);
                    padding-left: 28px;
                }
            }
        }
    }
}

.sp1_task_comment_list_items{
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 550px;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    padding: 10px;
}

.sp1_task_comment_list_items::-webkit-scrollbar{ 
    width: 4px;
    border-radius: 4px !important;
} 

.sp1_task_comment_list_items::-webkit-scrollbar-track{
    background: rgb(240, 240, 240) !important;
    border-radius: 4px;
    padding: 0 !important;
}

.sp1_task_comment_list_items::-webkit-scrollbar-thumb{
    border: none;
}

.sp1_task_comments{
    width: 100%; 
}


.sp1_task_comment_actions{
    width: 100%;
    margin-top: 6px;
    display: flex;
    align-items: center;
    gap: 6px;
    color: #777;
    padding-left: 16px;

    a{
        color: #777;
        font-size: 14px;
        
        &:hover{
            text-decoration: underline;
            color:#333 !important;
        }
    }

    & .replies_count{
        display: flex;
        align-items: center;
        position: relative;
        cursor: pointer;
        margin-left: 16px;

        & > .reply_auth_avatar{
            position: relative;
            display: flex;
            align-items: center;
            width: 32px; 

            & > div{
                display: block;
                position: absolute;
                left: 0; 
                border-radius: 50%;
                img{
                    border: 2px solid rgb(237, 237, 237);
                    box-shadow: 0 0 3px rgba(0,0,0,.1);
                }
            }   
        }

        &:hover{
            text-decoration: underline;
            color: rgb(39, 111, 236);
        }
    }
}


.sp1_task_comment_replies{
    width: 100%;
    max-width: 650px;
}



.sp1_task_card--sub-card-modal{
    display: flex;
    align-items: center;
    justify-content: center;
}

.sp1_task_card--sub-card-modal-content{
    width: 100%;
    max-width: 60vw;
    background: #fff;
    margin: auto; 
    overflow: hidden;
    border-radius: 10px; 

    & .__header{
        background-color: #ececec;
        padding: 6px 10px;
        margin-bottom: 10px;
        border: 1px solid #E7EFFC;
        button{
            margin-left: auto;
            font-size: 18px;
            margin-right: 10px;
            width: 28px;
            height: 28px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 6px;
            color: #5c5c5c;

            &:hover{
                background-color:#f3f3f3;
                color: #525252; 
            }
        }
    }

    & .__content{
        height: 100%;
        max-height: 70vh;
        overflow-x: hidden;
        overflow-y: auto;
        padding: 16px 32px;
    }

    & .__footer{
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        border-top: 1px dashed #eeeeee;
        box-shadow: 0 -2px 6px rgba(0,0,0,.1);

